<template>
    <el-container>
        <el-header>
            <v-head></v-head>
        </el-header>
        <el-main>
            <transition name="move" mode="out-in">
                <router-view></router-view>
            </transition>
        </el-main>
    </el-container>
</template>

<script>
    import vHead from './Header.vue';
    export default {
        components:{
            vHead
        }
    }
</script>


