<template>
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
        <el-menu-item index="1">首页</el-menu-item>
        <el-submenu index="2">
            <template slot="title">考试</template>
            <el-menu-item index="2-1">选项1</el-menu-item>
            <el-menu-item index="2-2">选项2</el-menu-item>
            <el-menu-item index="2-3">选项3</el-menu-item>
        </el-submenu>
        <el-menu-item index="3">问卷管理</el-menu-item>
        <el-submenu index="4">
            <template slot="title">系统管理</template>
            <el-menu-item index="4-1">权限管理</el-menu-item>
            <el-menu-item index="4-2">目录管理</el-menu-item>
            <el-menu-item index="4-3">日志管理</el-menu-item>
        </el-submenu>

        <el-menu-item index="5">登录</el-menu-item>
    </el-menu>
    <!-- <div class="user-info">
        <el-dropdown trigger="click" @command="handleCommand">
            <span class="el-dropdown-link">
                <img class="user-logo" src="../../../static/img/img.jpg">
                {{username}}
            </span>
            <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="loginout">退出</el-dropdown-item>
            </el-dropdown-menu>
        </el-dropdown>
    </div> -->
</template>
<script>
    export default {
        data() {
            return {
               activeIndex: '3',
            };
        },
        computed:{
            username(){
                let username = localStorage.getItem('ms_username');
                return username ? username : this.name;
            }
        },
        methods:{
            handleSelect(key, keyPath) {
                console.log(key, keyPath)
                if (key == 1) {
                   this.$router.push('/');
                } else if (key == 2) {
                    this.$router.push('/test');
                } else if (key == 3) {
                    this.$router.push('/manage');
                } else if (key == 5) {
                    this.$router.push('/login');
                } 
            }
            // handleCommand(command) {
            //     if(command == 'loginout'){
            //         localStorage.removeItem('ms_username')
            //         
            //     }
            // }
        }
    }
</script>
<style scoped>
    .el-menu {
        padding-left: 20px;
    }
</style>



